.container {
    position: absolute;
    top: 18rem;
    right: 35px;
    z-index: 1;
}
.nowLocation {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    border: 1px solid #ededed;
    box-shadow: 0px 2px 1px 0px rgba(153, 153, 153, 0.1);
    background: #fff;
    border-radius: 5px;
    cursor: pointer;
}
.nowLocation button {
    width: 1.125rem;
    height: 1.125rem;
    border: none;
    background: url(../images/location.png) center no-repeat;
    background-size: 1.125rem;
    cursor: pointer;
}
.container span {
    position: absolute;
    top: 5px;
    right: 4px;
    width: 77px;
    height: 22px;
    background: url('../images/select_now.png') no-repeat 3px;
    background-size: 75px 22px;
    font-size: 0.875rem;
    color: #fff;
}

.fade_in {
    animation: fadeIn 2.5s ease 0s 1 normal forwards;
  }

.fade_out {
    animation: fadeOut 1s ease 0s 1 normal forwards;
  }
@keyframes fadeIn {
    0% {
      opacity: 0;
    }
   33% {
    opacity: 1;
   }
   66% {
    opacity: 1;
   }
   100% {
    opacity: 0;
   }
  }